.notification {
  color: #fff;
  border-radius: 5px;
  align-items: center;
  margin: 10px;
  padding: 15px;
  display: flex;
  position: relative;
  box-shadow: 0 2px 4px #0003;
}

.slidein-left {
  animation: .5s ease-out slideInLeft;
}

.slidein-right {
  animation: .5s ease-out slideInRight;
}

.notification:after {
  content: "";
  width: 100%;
  height: 4px;
  animation: loader var(--animation-duration, 3s) linear 0s forwards;
  background-color: #888;
  position: absolute;
  bottom: 0;
  left: 0;
}

@keyframes popOut {
  from {
    opacity: 1;
    transform: scale(1);
  }

  to {
    opacity: 0;
    transform: scale(.8);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(100%);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-100%);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes loader {
  0% {
    width: 100%;
  }

  100% {
    width: 0;
  }
}

.success {
  background-color: #4caf50;
}

.danger {
  background-color: #f44336;
}

.info {
  background-color: #2196f3;
}

.warning {
  background-color: #ff9800;
}

.top-left {
  position: fixed;
  top: 20px;
  left: 20px;
}

.top-right {
  position: fixed;
  top: 20px;
  right: 20px;
}

.bottom-right {
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.bottom-left {
  position: fixed;
  bottom: 20px;
  left: 20px;
}

.close-icon {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  margin-left: 5px;
  display: flex;
}

.close-icon:hover {
  color: #000;
}
